<template>
  <div class="shortcutContent" :style="{ left: left + 'px', top: top + 'px' }" v-if="contextmenuShow">
    <div class="shortcut" @click="handlerClick('插入下级节点')">
      <span>插入下级节点</span>
      <span>Tab | Insert</span>
    </div>
    <div class="shortcut" v-if="!currentNode?.isRoot" @click="handlerClick('插入同级节点')">
      <span>插入同级节点</span>
      <span>Enter</span>
    </div>
    <div class="shortcut" v-if="!currentNode?.isRoot" @click="handlerClick('插入父节点')">
      <span>插入父节点</span>
      <span>Shift + Tab</span>
    </div>
    <div class="shortcut divider" @click="handlerClick('展开所有下级节点')">
      <span>展开所有下级节点</span>
      <span>/</span>
    </div>
    <div class="shortcut" @click="handlerClick('收起所有下级节点')">
      <span>收起所有下级节点</span>
      <span>/</span>
    </div>
    <div class="shortcut" @click="handlerClick('上移节点')" v-if="moveState.canMoveUp && !currentNode?.isRoot">
      <span>上移节点</span>
      <span>Delete | ↑</span>
    </div>
    <div class="shortcut" @click="handlerClick('下移节点')" v-if="moveState.canMoveDown && !currentNode?.isRoot">
      <span>下移节点</span>
      <span>Shift + ↓</span>
    </div>
    <div class="shortcut divider" @click="handlerClick('删除所有下级节点')">
      <span style="color: #f56c6c">删除所有下级节点</span>
      <span>Delete | Backspace</span>
    </div>
    <div class="shortcut" v-if="!currentNode?.isRoot" @click="handlerClick('仅删除当前节点')">
      <span style="color: #f56c6c">仅删除当前节点</span>
      <span>Shift + Backspace</span>
    </div>
    <div class="shortcut" @click="handlerClick('编辑节点')">
      <span>编辑节点</span>
      <span>F2</span>
    </div>
    <div class="shortcut" @click="handlerClick('添加备注')">
      <span style="color: #000">添加备注</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    mindMapData: {
      type: Object,
    },
  },
  data() {
    return {
      contextmenuShow: false,
      currentNode: null,
      left: 0,
      top: 0,
    }
  },
  computed: {
    //判断上移下移按钮是否可用
    moveState() {
      if (this.currentNode) {
        const targetNode = this.findNodeById(this.mindMapData, this.currentNode.uid)
        if (!targetNode || !targetNode.parent) return { canMoveUp: false, canMoveDown: false }

        const siblings = targetNode.parent.children
        const currentIndex = siblings.findIndex((n) => n.data.uid === this.currentNode.uid)

        return {
          canMoveUp: siblings.length > 1 && currentIndex > 0,
          canMoveDown: siblings.length > 1 && currentIndex < siblings.length - 1,
        }
      }
    },
  },
  methods: {
    // 根据uid查找节点
    findNodeById(node, uid, parent = null) {
      if (node.data.uid === uid) return { node, parent }
      for (const child of node.children || []) {
        const result = this.findNodeById(child, uid, node)
        if (result) return result
      }
      return null
    },
    // 节点右键菜单
    nodeContextMenu(e, node) {
      this.contextmenuShow = true
      const menuWidth = 360 // 菜单宽度
      const menuHeight = 350 + 20 // 菜单高度

      // 获取屏幕宽度和高度
      const screenWidth = window.innerWidth
      const screenHeight = window.innerHeight

      // 计算菜单的位置
      let left = e.clientX
      let top = e.clientY

      // 检查菜单是否会超出屏幕右侧
      if (left + menuWidth > screenWidth) {
        left = screenWidth - menuWidth
      }

      // 检查菜单是否会超出屏幕底部
      if (top + menuHeight > screenHeight) {
        top = screenHeight - menuHeight
      }

      this.left = left
      this.top = top - 20
      this.currentNode = node
    },
    // 快捷键点击事件
    handlerClick(text) {
      switch (text) {
        case '插入下级节点':
          this.$parent.mindMap.execCommand('INSERT_CHILD_NODE')
          this.contextmenuShow = false
          break
        case '插入同级节点':
          this.$parent.mindMap.execCommand('INSERT_NODE')
          this.contextmenuShow = false
          break
        case '插入父节点':
          this.$parent.mindMap.execCommand('INSERT_PARENT_NODE')
          this.contextmenuShow = false
          break
        case '展开所有下级节点':
          this.$parent.mindMap.execCommand('EXPAND_ALL', this.currentNode.uid)
          this.contextmenuShow = false
          break
        case '收起所有下级节点':
          this.$parent.mindMap.execCommand('UNEXPAND_ALL', false, this.currentNode.uid)
          this.contextmenuShow = false
          break
        case '上移节点':
          this.$parent.mindMap.execCommand('UP_NODE')
          this.contextmenuShow = false
          break
        case '下移节点':
          this.$parent.mindMap.execCommand('DOWN_NODE')
          this.contextmenuShow = false
          break
        case '删除所有下级节点':
          this.$parent.mindMap.execCommand('REMOVE_NODE')
          this.contextmenuShow = false
          break
        case '仅删除当前节点':
          this.$parent.mindMap.execCommand('REMOVE_CURRENT_NODE')
          this.contextmenuShow = false
          break
        case '编辑节点':
          this.$nextTick(() => {
            setTimeout(() => {
              this.$parent.mindMap.renderer.textEdit.show({ node: this.currentNode })
            }, 100)
          })
          this.contextmenuShow = false
          break
        case '添加备注':
          this.$prompt('请输入备注内容', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            inputErrorMessage: '请输入备注内容',
            inputAttributes: {
              required: true,
            },
            inputValidator: (value) => {
              if (!value) {
                return '请输入备注内容'
              }
              return true
            },
          })
            .then(({ value }) => {
              this.currentNode.setNote(value)
              this.$message.success('添加备注成功')
            })
            .catch(() => {})
          this.contextmenuShow = false
          break
      }
    },
  },
  created() {},
  mounted() {},
}
</script>

<style lang="scss" scoped>
.shortcutContent {
  width: 300px;
  position: fixed;
  z-index: 9999;
  box-shadow: 0px 3px 7px 0px rgba(94, 110, 136, 0.2);
  background-color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  padding: 10px 0;
  i {
    font-size: 16px;
    margin-right: 6px;
  }
  .shortcut {
    width: 100%;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    user-select: none;
    padding: 6px 14px;
    &:hover {
      background-color: #f5f5f5;
    }
    span {
      margin-left: 4px;
      cursor: pointer;
      &:last-child {
        color: #666;
      }
    }
  }
  .divider {
    border-top: 1px solid #eee;
  }
}
</style>
